import React, { useState } from "react";
import emailjs from "emailjs-com";
import { useForm } from "react-hook-form";
import { Alert } from "react-bootstrap";

const Contact = () => {

    const [successMessage, setSuccessMessage] = useState("");
    const [error, setError] = useState("")
    const { register, handleSubmit, errors } = useForm();

    const serviceID = "service_ID";
    const templateID = "template_ID";
    const userID = "user_gpUoGFUDSK75XsHP8pYqn";

    const onSubmit = (data, r) => {
        console.log(data)
        sendEmail(
          serviceID,
          templateID,
          {
            name: data.name,
            phone: data.phone,
            email: data.email,
            subject: data.subject,
            description: data.description
          },
          userID
        )
        r.target.reset();
    }

    const sendEmail = (serviceID, templateID, variables, userID) => {
        emailjs.send(serviceID, templateID, variables, userID)
          .then(() => {
            setSuccessMessage("I have recieved your message! I'll contact you as soon as I can.");
          }).catch(() => setError(`Something went wrong`));
    }

    return (
        <div id="contact" className="contacts">
            <div className="text-center">
                <h1>contact me</h1>
                <p>Please fill out the form and describe your reasons and I will contact you as soon as possible</p>
            </div>
            <div className="container">
                {successMessage && <Alert variant="success" className="text-center">{successMessage}</Alert>}
                {error && <Alert variant="danger">{error}</Alert>}
            <form onSubmit={handleSubmit(onSubmit)}>
            <div className="row">
                <div className="col-md-6 col-xs-12">
                {/* NAME INPUT */}
                <div className="text-center">
                    <input
                    type="text"
                    className="form-control"
                    placeholder="Name"
                    name="name"
                    ref={
                        register({
                        required: "Please enter your name",
                        maxLength: {
                            value: 20,
                            message: "Please enter a name with fewer than 20 characters"
                        }
                        })
                    }
                    />
                    <div className="line"></div>
                </div>
                <span className="error-message">
                    {errors.name && errors.name.message}
                </span>
                {/* PHONE INPUT */}
                <div className="text-center">
                    <input
                    type="text"
                    className="form-control"
                    placeholder="Phone Number"
                    name="phone"
                    ref={
                        register({
                        required: "Please add your phone number",
                        })
                    }
                    />
                    <div className="line"></div>
                </div>
                <span className="error-message">
                    {errors.phone && errors.phone.message}
                </span>
                {/* EMAIL INPUT */}
                <div className="text-center">
                    <input
                    type="email"
                    className="form-control"
                    placeholder="Email"
                    name="email"
                    ref={
                        register({
                        required: "Please provide your email",
                        pattern: {
                            value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                            message: "invalid Email"
                        }
                        })
                    }
                    />
                    <div className="line"></div>
                </div>
                <span className="error-message">
                    {errors.email && errors.email.message}
                </span>
                {/* SUBJECT INPUT */}
                <div className="text-center">
                    <input
                    type="text"
                    className="form-control"
                    placeholder="Subject"
                    name="subject"
                    ref={
                        register({
                        required: "OOPS, you forget to add the subject.",
                        })
                    }
                    />
                    <div className="line"></div>
                </div>
                <span className="error-message">
                    {errors.subject && errors.subject.message}
                </span>
                </div>
                <div className="col-md-6 col-xs-12">
                {/* DESCRIPTION */}
                <div className="text-center">
                    <textarea
                    type="text"
                    className="form-control"
                    placeholder="Please describe shortly your reason for contacting me..."
                    name="description"
                    ref={
                        register({
                        required: "Please describe shortly your reasons for contacting me...",
                        })
                    }
                    ></textarea>
                    <div className="line"></div>
                </div>
                <span className="error-message">
                    {errors.description && errors.description.message}
                </span>
                <button className="btn-main-offer contact-btn" type="submit">contact me</button>
                </div>
            </div>
            </form>
      </div>
    </div>
        
    )
}

export default Contact
